import * as React from 'react';
import { styled, alpha } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import { NavLink } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "../app/hooks";
import { getSearchInputValue, getSelectedUserID, setSearchInputValue } from "../features/UserList/userListSlice";
import { ChangeEvent } from "react";
import { SidebarMenu } from "./SidebarMenu";

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(1),
    width: 'auto',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
}));

const HeaderLink = styled(NavLink)(() => ({
  color: '#000',
  textDecoration: 'none',
  transition: 'all 0.3s',
  '&:hover': {
    textDecoration: 'underline',
  },
}));

export const Header = () => {
  const dispatch = useAppDispatch();
  const selectedUserId = useAppSelector(getSelectedUserID);
  const searchInputValue = useAppSelector(getSearchInputValue);

  const changeInputHandler = ({ target }: ChangeEvent<HTMLTextAreaElement | HTMLInputElement>): void => {
    dispatch(setSearchInputValue(target.value));
  }

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static" sx={{ bgcolor: '#fff' }}>
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="open drawer"
            sx={{ mr: 2, display: { sm: 'none', xs: 'block' }}}
          >
            <SidebarMenu />
          </IconButton>
          <Typography
            variant="h6"
            noWrap
            component="div"
            sx={{
              flexGrow: 1,
              display: { xs: 'none', sm: 'flex' },
              width: 'max-content',
              gap: '10px',
          }}
          >
            <HeaderLink to="/">Home</HeaderLink>
            <HeaderLink to="/create-user">Create user</HeaderLink>
          </Typography>
          {!selectedUserId && (
            <Search>
              <SearchIconWrapper>
                <SearchIcon />
              </SearchIconWrapper>
              <StyledInputBase
                placeholder="Search…"
                inputProps={{ 'aria-label': 'search' }}
                value={searchInputValue}
                onChange={changeInputHandler}
              />
            </Search>
          )}
        </Toolbar>
      </AppBar>
    </Box>
  );
}
